<template>
  <!--container-->
  <section class="container">

    <!--questionBox-->
    <div class="questionBox" id="app">

      <!-- transition -->
      <transition :duration="{ enter: 500, leave: 300 }" enter-active-class="animated zoomIn"
                  leave-active-class="animated zoomOut" mode="out-in">

        <!--qusetionContainer-->
        <div class="questionContainer" v-if="questionIndex<quiz.questions.length" v-bind:key="questionIndex">

          <header>
            <!--progress-->
            <div class="progressContainer">
              <progress class="progress is-info is-small" :value="(questionIndex/quiz.questions.length)*100" max="100">
                {{(questionIndex / quiz.questions.length) * 100}}%
              </progress>
              <p>{{( questionIndex / quiz.questions.length) * 100}}% аяқталды</p>
            </div>
            <!--/progress-->
          </header>

          <!-- questionTitle -->
          <h2 class="titleContainer title">{{ quiz.questions[questionIndex].text }}</h2>

          <!-- quizOptions -->
          <div class="optionContainer">
            <div class="option" v-for="(response, index) in quiz.questions[questionIndex].responses"
                 @click="selectOption(index)" :class="{ 'is-selected': userResponses[questionIndex] == index}"
                 :key="index">
              {{ index | charIndex }}. {{ response.text }}
            </div>
          </div>

          <!--quizFooter: navigation and progress-->
          <footer class="questionFooter">

            <!--pagination-->
            <nav class="pagination" role="navigation" aria-label="pagination">

              <!-- back button -->
              <a class="button" v-on:click="prev();" :disabled="questionIndex < 1">
                Артқа
              </a>

              <!-- next button -->
              <a class="button" :class="(userResponses[questionIndex]==null)?'':'is-active'" v-on:click="next();"
                 :disabled="questionIndex>=quiz.questions.length">
                {{ (userResponses[questionIndex] == null) ? 'Өткізіп жіберу' : 'Келесі' }}
              </a>

            </nav>
            <!--/pagination-->

          </footer>
          <!--/quizFooter-->

        </div>
        <!--/questionContainer-->

        <!--quizCompletedResult-->
        <div v-if="questionIndex >= quiz.questions.length" v-bind:key="questionIndex"
             class="quizCompleted has-text-centered">

          <!-- quizCompletedIcon: Achievement Icon -->
          <span class="icon">
                <i class="fa" :class="score()>3?'fa-check-circle-o is-active':'fa-times-circle'"></i>
              </span>

          <!--resultTitleBlock-->
          <!--<h2 class="title">-->
          <!--Сіз тестті!-->
          <!--</h2>-->
          <p class="subtitle">
            Жалпы бағасы: {{ score() }} / {{ quiz.questions.length }}
          </p>
          <br>
          <a class="button" @click="restart()">қайтадан <i class="fa fa-refresh"></i></a>
          <!--/resultTitleBlock-->

        </div>
        <!--/quizCompetedResult-->

      </transition>

    </div>
    <!--/questionBox-->

  </section>
  <!--/container-->
</template>

<script>

  export default {
    data() {
      return {
        quiz: null,
        questionIndex: 0,
        userResponses: null,
        isActive: false
      }
    },
    filters: {
      charIndex: function (i) {
        return String.fromCharCode(97 + i);
      }
    },
    methods: {
      restart: function () {
        this.questionIndex = 0;
        this.userResponses = Array(this.quiz.questions.length).fill(null);
      },
      selectOption: function (index) {
        this.$set(this.userResponses, this.questionIndex, index);
        //console.log(this.userResponses);
      },
      next: function () {
        if (this.questionIndex < this.quiz.questions.length)
          this.questionIndex++;
      },

      prev: function () {
        if (this.quiz.questions.length > 0) this.questionIndex--;
      },
      // Return "true" count in userResponses
      score: function () {
        var score = 0;
        for (let i = 0; i < this.userResponses.length; i++) {
          if (
            typeof this.quiz.questions[i].responses[
              this.userResponses[i]
              ] !== "undefined" &&
            this.quiz.questions[i].responses[this.userResponses[i]].correct
          ) {
            score = score + 1;
          }
        }
        return score;

        //return this.userResponses.filter(function(val) { return val }).length;
      }
    },
    created() {
      this.quiz = window.quiz;
      this.userResponses = Array(this.quiz.questions.length).fill(null);
    }
  }
</script>
<style>
  .button {
  transition: 0.3s;
}
.title,
.subtitle {
  font-family: Montserrat, sans-serif;
  font-weight: normal;
}
.animated {
  transition-duration: 0.15s;
}
.questionBox {
  background: #FAFAFA;
  position: relative;
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.questionBox header {
    background: rgba(0, 0, 0, 0.025);
    padding: 1.5rem;
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.questionBox header h1 {
      font-weight: bold;
      margin-bottom: 1rem !important;
}
.questionBox header .progressContainer {
      width: 60%;
      margin: 0 auto;
}
.questionBox header .progressContainer > progress {
        margin: 0;
        border-radius: 5rem;
        overflow: hidden;
        border: none;
        color: #3D5AFE;
}
.questionBox header .progressContainer > progress::-moz-progress-bar {
          background: #3D5AFE;
}
.questionBox header .progressContainer > progress::-webkit-progress-value {
          background: #3D5AFE;
}
.questionBox header .progressContainer > p {
        margin: 0;
        margin-top: 0.5rem;
}
.questionBox .titleContainer {
    text-align: center;
    margin: 0 auto;
    padding: 1.5rem;
}
.questionBox .quizForm {
    display: block;
    white-space: normal;
    height: 100%;
    width: 100%;
}
.questionBox .quizForm .quizFormContainer {
      height: 100%;
      margin: 15px 18px;
}
.questionBox .quizForm .quizFormContainer .field-label {
        text-align: left;
        margin-bottom: 0.5rem;
}
.questionBox .quizCompleted {
    width: 100%;
    padding: 1rem;
    text-align: center;
}
.questionBox .quizCompleted > .icon {
      color: #FF5252;
      font-size: 5rem;
}
.questionBox .quizCompleted > .icon .is-active {
        color: #00E676;
}
.questionBox .questionContainer {
    white-space: normal;
    height: 100%;
    width: 100%;
}
.questionBox .questionContainer .optionContainer {
      margin-top: 12px;
      flex-grow: 1;
}
.questionBox .questionContainer .optionContainer .option {
        border-radius: 290486px;
        padding: 9px 18px;
        margin: 0 18px;
        margin-bottom: 12px;
        transition: 0.3s;
        cursor: pointer;
        background-color: rgba(0, 0, 0, 0.05);
        color: rgba(0, 0, 0, 0.85);
        border: transparent 1px solid;
}
.questionBox .questionContainer .optionContainer .option.is-selected {
          border-color: rgba(0, 0, 0, 0.25);
          background-color: white;
}
.questionBox .questionContainer .optionContainer .option:hover {
          background-color: rgba(0, 0, 0, 0.1);
}
.questionBox .questionContainer .optionContainer .option:active {
          transform: scaleX(0.9);
}
.questionBox .questionContainer .questionFooter {
      background: rgba(0, 0, 0, 0.025);
      border-top: 1px solid rgba(0, 0, 0, 0.1);
      width: 100%;
      align-self: flex-end;
}
.questionBox .questionContainer .questionFooter .pagination {
        margin: 15px 25px;
}
.pagination {
  display: flex;
  justify-content: space-between;
}
.button {
  padding: 0.5rem 1rem;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 5rem;
  margin: 0 0.25rem;
  transition: 0.3s;
}
.button:hover {
    cursor: pointer;
    background: #ECEFF1;
    border-color: rgba(0, 0, 0, 0.25);
}
.button.is-active {
    background: #3D5AFE;
    color: white;
    border-color: transparent;
}
.button.is-active:hover {
      background: #0a2ffe;
}
@media screen and (min-width: 769px) {
.questionBox {
    align-items: center;
    justify-content: center;
}
.questionBox .questionContainer {
      display: flex;
      flex-direction: column;
}
}
@media screen and (max-width: 768px) {
.sidebar {
    height: auto !important;
    border-radius: 6px 6px 0px 0px;
}
}

</style>



